.App {
  text-align: center;
}

.App-logo {
  margin-left: -25px;
  margin-bottom: 10px;
  display: block;
  height: 40vmin;
  pointer-events: none;
  user-select: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.title{
  color: #fff;
  
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.card-pers{
  color: #000;
  font-size: 25px;
}
.margen{
  margin-top: 15px;
}
h3 {
  margin-top: 10px;
}
.length{
  margin-left: 20px;
}